<template>
  <div class="hello">
    <h1>Music Maker</h1>
    <div>Select a synth and play some chords ...</div>
    <div class="synth-row">
      <div class="synth-cell" v-bind:class="{'synth-selected':synth.selected}" v-for="(synth, iS) of synths" v-bind:key="iS"
            v-on:click="loadSynth(iS)">
        <span class="synth-name">{{synth.name}}</span> 
      </div>
    </div>
    <div class="grid" v-for="(root, iR) of roots" v-bind:key="iR">
      <div class="grid-row">
        <div class="grid-cell" v-for="(pattern, iP) of patterns" v-bind:key="iP"
             v-on:click="play(iR, iP)">
          <span class="note-name">{{root}}</span><span class="pattern-name">{{pattern.name}}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Tone from "tone";
export default {
  name: "Music",
  props: {},
  data: function(){
    return {
      synth:null,
      synths:[
        { name: 'M0', o: Tone.Monotone },
        { name: 'M1', o: Tone.MonoSynth },
        { name: 'Duo', o: Tone.DuoSynth },
        { name: 'FM', o: Tone.FMSynth },
        { name: 'AM', o: Tone.AMSynth }
      ],
      patterns:[
        { name: 'min9', intervals:[-24,-12,2,3,7,10] },
        { name: 'maj9', intervals:[-24,-12,2,4,7,11] },
        { name: '7#9', intervals:[-24,-12,3,4,8,10,13]},
        { name: '11', intervals:[-24,-12,5,10,14] }
      ],
      roots:['C','Db','D','Eb','E','F','Gb','G','Ab','A','Bb','B']
    }
  },
  computed:{},
  methods:{
    play:function(rootIndex, patternIndex){
      let nOctave = 5;
      let rootNote = this.roots[rootIndex] + nOctave;
      let intervals = this.patterns[patternIndex].intervals;
      let rootFrequency = new Tone.Frequency(rootNote);
      let chord = rootFrequency.harmonize(intervals);
      this.synth.triggerAttackRelease(chord, "8n");
    },
    loadSynth: function(synthIndex){
      //if(!this.synth) this.synth.disconnect(); // TODO: Or should it be dispose()?
      let synth = new Tone.PolySynth(6, this.synths[synthIndex].o, {
        oscillator : {
          type : "sine"
        }
      }).connect(this.nextNode);
      //set the attributes using the set interface
      synth.set("detune", -1200);
      //play a chord
      this.synth = synth;
      this.synths.forEach((synth, index) => {
        synth.isSelected = false;
        if(index === synthIndex) synth.isSelected = true;
      });
    }
  },
  mounted(){
    this.Tone = Tone;
    this.Tone.context.lookAhead = 0;
    //let autoWah = new Tone.AutoWah().toMaster();
    //autoWah.Q.value = 6;
    //autoWah.sensitivity = -20;
    let chorus = new Tone.Chorus().toMaster();//.connect(autoWah);
    //a polysynth composed of 6 Voices of Synth
    this.nextNode = chorus;
    this.loadSynth(0)
  }
};
</script>

